.Button {
    background: #eb008d none;
    border-radius: 2rem;
    border: 1px solid transparent;
    color: white;
    outline: 0;
    text-shadow: 0 1px rgba(0,0,0,.08);
    -webkit-box-shadow: 0 0 0 1px #eb008d;
            box-shadow: 0 0 0 1px #eb008d;
    font-size: 16px;

    display: inline-block;
    margin-bottom: 0;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    white-space: nowrap;
    padding: 8px 25px;
    line-height: 1.6;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}


