.panel {
    padding: 20px 10px;
    background-color: #eee;
}
.active-panel {
    background: white;
}

input {
    color: #0c80ef;
    text-shadow: 0px 0px 0px black;
    -webkit-text-fill-color: transparent;
    background: transparent;
    width: 100%;
    outline: none;
    border: 0;
    height: 20vh;
    text-align: right;
    font-size: 50px;
    font-weight: 300;
}

input::-webkit-input-placeholder{
    color: #ddd;  /* change [placeholder color] by this*/
    text-shadow: none;
    -webkit-text-fill-color: initial;
}

input:-ms-input-placeholder{
    color: #ddd;  /* change [placeholder color] by this*/
    text-shadow: none;
    -webkit-text-fill-color: initial;
}

input::placeholder{
    color: #ddd;  /* change [placeholder color] by this*/
    text-shadow: none;
    -webkit-text-fill-color: initial;
}


